export default {
    getCategoryOne: config => {
      let arr=['Dress','Kids','Electronics','Jewelry','Home & Kitchen','Beauty & Personal Care','Decorative Flowers','Stationeries'];
      let mapping=[0,1,6,4,3,5,2,7];
      let res=[];
      for (let index = 0; index < arr.length; index++) {
          const element = arr[index];
         res.push({
            "categoryName": element,
            "categoryCode":"0"+(index+1),
            "img": require('@/common/images/icon_manue_0'+(mapping[index]+2)+'.png')
         });
      }

      return {code:200000,result:res};
  }
}


