<template>
        <!--左侧蝜坕-->
    <van-popup
      v-model="show"
      position="left"
      style="height: 100%;width:55%; min-width: 235px;"
    >
      <div class="manueWrap">
        <div class="userHead">
          <div @click="$router.push('center')">

            <p>
              <img
                class="img"
                v-if="user&&user.headImage"
                :src="user.headImage"
                alt=""
              />
              <img
                class="img"
                v-if="!user||!user.headImage"
                src="static/img/defaultHead.1438521.png"
                alt=""
              />
            </p>
            <p style="margin-top: 6px;">{{ user&&user.userName }}</p>
            <p class="gray">{{ user&&user.email }}</p>
          </div>
        </div>
        <ul class="clearfix">
          <li>
            <a @click="backHome">
            <!-- <a @click="show = false"> $router.push('/')-->
              <span class="imgWrap"
                ><img src="../../common/images/icon_manue_01.png" /> </span
              ><span>Home</span>
            </a>
          </li>
        </ul>
        <ul class="clearfix">
          <li
            v-for="(item, index) in categoryOne"
            :key="index"
            @click="
              $router.push({
                path: '/categoryElectronics',
                query: {
                  categoryCode: item.categoryCode,
                  name: item.categoryName
                }
              })
            "
          >
            <a href="javascript:void(0)">
              <span class="imgWrap"><img :src="item.img"/></span
              ><span>{{ item.categoryName }}</span></a
            >
          </li>

          <li @click="$router.push('allCategories')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../../common/images/icon_manue_10.png"/></span
              ><span>View all categories</span></a
            >
          </li>
        </ul>
        <ul class="clearfix">
          <li @click="$router.push('orderList')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../../common/images/icon_manue_11.png" /><em
                  class="bag"
                >
                  <i class="font10"> 99 </i></em
                ></span
              ><span>Orders</span></a
            >
          </li>
          <li @click="$router.push('whislist')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../../common/images/icon_manue_12.png"/></span
              ><span>Whislist</span></a
            >
          </li>
          <li @click="$router.push('center')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../../common/images/icon_manue_13.png"
              /></span>
              <span>My Acount</span></a
            >
          </li>

          <li @click="$router.push('mystores')" v-if="user&&user.level * 1 > 1">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../../common/images/icon_manue_14.png"
              /></span>
              <span>My VIP</span></a
            >
          </li>
          <li @click="$router.push('becomeVip')" v-else>
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../../common/images/icon_manue_14.png"
              /></span>
              <span>Become VIP</span>
              </a>
          </li>

          <li @click="$router.push('browseRecord')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../../common/images/icon_manue_15.png"/></span
              ><span>Browse Records</span></a
            >
          </li>
          <li @click="$router.push('merchantNotice')">
            <a href="javascript:void(0)"
              ><span class="imgWrap"
                ><img src="../../common/images/icon_manue_16.png" /><em
                  class="bag"
                >
                  <b class="iconfont">&#xe63b;</b></em
                ></span
              ><span>Notifications</span></a
            >
          </li>
        </ul>
      </div>
    </van-popup>
</template>
<script>
import { mapGetters } from "vuex";
export default {
    data(){
        return {
          show: false,
        }
    },
    computed: {
    ...mapGetters([
      "indexData",
      "productData",
      "user",
      "categoryOne",
      "wishCode",
      "wishes"
    ]),
    },
    methods: {
      showDrawer(){
        this.$store.dispatch("getCategoryOne", { level: 1 }).then(res => {});
          this.show=true;
      },
      backHome(){
        this.show = false;
        this.$router.push('/');
      }
    },
    watch: {
      "$route.matched":function(){
        this.show=false;
      }
    },
}
</script>

<style lang="scss" scoped>

.photo{
      position: relative;
      width: 79px;
      height: 79px;
      border-radius: 50%;
      background: url('../../common/images/defaultHead.png') no-repeat;
      background-size: cover;
  }
</style>




