import { myCarts, checkCart, deleteCart, updateCart, addCart, queryBySkuCode,synchronizeCart } from '@/api/cart'
import store from '../index'
let getLocalStorge = (key) => {
    if (localStorage.getItem(key)) {
        try {
            return JSON.parse(localStorage.getItem(key));
        } catch (e) {
            console.log(e);
            localStorage.removeItem(key);
        }
    }
}

const state = {
    carts: getLocalStorge('carts') || {},
    proDetail:{}
}


const mutations = {
    setCarts(state, carts) {
        state.carts = carts;
    },
    addCart(state, cart) {
        let carts = state.carts.cartProductList.filter(e => { return e.skuCode == cart.skuCode; });
        if (carts.length > 0) {
            carts[0].num++;
        } else {
            cart.id = state.carts.cartProductList.length + 1;
            state.carts.cartProductList.push(cart);
        }
        calCart(state.carts);
        localStorage.setItem('carts', JSON.stringify(state.carts));
    },
    updateCart(state, { id, num, skuCode, sku }) {
        let carts = state.carts.cartProductList.filter(e => { return e.id == id; });
        if (carts.length > 0) {
            if (carts[0].skuCode == skuCode) {
                carts[0].num = num;
            } else {
                sku.id = carts[0].id;
                state.carts.cartProductList.splice(carts[0].id - 1,1,sku);
            }

            calCart(state.carts);
            localStorage.setItem('carts', JSON.stringify(state.carts));
        }
    },
    deleteCart(state,{id}){
        let carts = state.carts.cartProductList.filter(e => { return e.id == id; });
        if (carts.length > 0) {
            state.carts.cartProductList.splice(carts[0].id - 1,1);
            calCart(state.carts);
            localStorage.setItem('carts', JSON.stringify(state.carts));
        }
    },
    clearCart(state){
        state.carts={};
        localStorage.removeItem('carts');
    },
    setProDetail(state,{proDetail}){
        state.proDetail=proDetail; 
    }
}

const checkLogin = () => {
    return store.getters.token != null;
}

const calCart = (carts) => {
    let totalNum = 0, totalPrice = 0;
    carts.cartProductList.forEach(el => {
        totalNum += el.num;
        if (el.settlementSku) {
            totalPrice += el.num * el.settlementSku.merchantPrice;
        }
    });
    carts.totalNum = totalNum;
    carts.totalPrice = totalPrice;
}

const actions = {
    myCarts({ commit }) {
        return new Promise((resolve, reject) => {
            if (checkLogin()) {
                myCarts().then(res => {
                    commit('setCarts', res.data.result)
                    resolve( res.data.result)
                }).catch(error => {
                    reject(error)
                })
            } else {
                if (!store.getters.carts.cartProductList) {
                    commit('setCarts', {
                        "totalPrice": 0,
                        "totalPreferentialPrice": 0,
                        "totalNum": 0,
                        "message": "",
                        "cartProductList": [],
                        "cartRecommendList": []
                    });
                }

                resolve(store.getters.carts);
            }
        })
    },
    addCart({ commit }, { skuCode, num }) {
        return new Promise((resolve, reject) => {
            if (checkLogin()) {
                addCart("", skuCode, num).then(res => {
                    resolve(res)
                }).catch(error => {
                    reject(error)
                })
            } else {
                if (!store.getters.carts.cartProductList) {
                    commit('setCarts', {
                        "totalPrice": 0,
                        "totalPreferentialPrice": 0,
                        "totalNum": 0,
                        "message": "",
                        "cartProductList": [],
                        "cartRecommendList": []
                    });
                }
                queryBySkuCode(skuCode, num).then(res => {
                    commit('addCart', res.data.result)
                    resolve(res)
                }).catch(error => {
                    reject(error)
                })
            }
        })
    },
    checkCart({ commit }, { id }) {
        if (checkLogin()) {
            return new Promise((resolve, reject) => {
                checkCart(id).then(res => {
                    resolve(res)
                }).catch(error => {
                    reject(error)
                })
            })
        } else {

        }
    },
    deleteCart({ commit }, { id }) {
        return new Promise((resolve, reject) => {
            if (checkLogin()) {
                deleteCart(id).then(res => {
                    resolve()
                }).catch(error => {
                    reject(error)
                })
            } else {
                commit('deleteCart', { id });
                resolve()
            }
        })
    },
    updateCart({ commit }, { id, num, skuCode }) {
        return new Promise((resolve, reject) => {
            if (checkLogin()) {
                updateCart(id, num, skuCode).then(res => {
                    resolve()
                }).catch(error => {
                    reject(error)
                })
            } else {
                queryBySkuCode(skuCode, num).then(res => {
                    commit('updateCart', { id, num, skuCode, sku: res.data.result });
                    resolve(res);
                }).catch(error => {
                    reject(error)
                })

            }
        })
    },
    syncCarts({commit}){
        let carts=getLocalStorge('carts');
        if(carts&&carts.cartProductList.length>0){
            synchronizeCart({cartListJson:JSON.stringify(carts.cartProductList)}).then(res=>{
                commit('clearCart');
            });
        }
    },
    proDetail({commit},{proDetail}){
        commit('setProDetail',{proDetail});
    }
}

export default {
    state,
    mutations,
    actions
}



