router.beforeEach((to, from, next) => {
  // ...
  let isNeedLogin=needLoginUrls.indexOf(to.path)>-1;
  if(isNeedLogin){
    let token=store.getters.token;
    if(!token){
        next({path:'/login',query:{'from':to.fullPath}});
        return false;
    }
  }
  next();
})

export default router;


