import { indexData, productData } from '@/api/index'

const state = {
    indexData: {},
    productData: {}
}



const mutations = {
    setIndexData(state, indexData) {
        state.indexData = indexData;
    },
    setProductData(state, productData) {
        state.productData = productData;
    }
}


const actions = {
    commitIndexData: ({ commit }, indexData) => commit('setIndexData', indexData),
    commitProductData: ({ commit }, productData) => commit('setProductData', productData),
    indexData({ commit },params) {
        return new Promise((resolve, reject) => {
            indexData(params).then(res => {
                commit('setIndexData', res.data.result)
                resolve(res.data.result)
            }).catch(error => {
                reject(error)
            })
        })
    },
    productData({ commit }) {
        return new Promise((resolve, reject) => {
            productData().then(res => {
                commit('setProductData', res.data.result)
                resolve(res.data.result)
            }).catch(error => {
                reject(error)
            })
        })
    }
}

export default {
    state,
    mutations,
    actions
}



