import { getFlashSaleDay } from '@/api/flashSale'

const state = {
    flashSaleDay: {},
}


const mutations = {
    setFlashSaleDay(state, flashSaleDay) {
        state.flashSaleDay = flashSaleDay;
    }
}


const actions = {
    getFlashSaleDay({ commit }, {pageNo, pageSize}) {
        return new Promise((resolve, reject) => {
            getFlashSaleDay(pageNo, pageSize).then(res => {
                commit('setFlashSaleDay', res.data.result.result)
                resolve(res.data.result.result)
            }).catch(error => {
                reject(error)
            })
        })
    }
}

export default {
    state,
    mutations,
    actions
}



