import { getProListByName, getCategoryOne,getCategoryTwo,getProDetailBySpuCode} from '@/api/product'

const state = {
    searchProductList: {},
    categoryOne:[],
    categoryTwo:[],
    productDetail:{},
}



const mutations = {
    setSearchProductList(state, productList) {
        state.searchProductList = productList;
    },
    setCategoryOne(state, categoryOne) {
        state.categoryOne = categoryOne;
    },
    setCategoryTwo(state,categoryTwo) {
        state.categoryTwo= categoryTwo;
    },
    setProductDetail(state,productDetail) {
        state.productDetail= productDetail;
    },
}


const actions = {
    getProListByName({ commit }, params) {
        return new Promise((resolve, reject) => {
            getProListByName(params).then(res => {
                commit('setSearchProductList', res.data.result)
                resolve()
            }).catch(error => {
                commit('setSearchProductList', {})
                reject(error)
            })
        })
    },
    getCategoryOne({ commit }, {level}) {
        return new Promise((resolve, reject) => {
            getCategoryOne(level).then(res => {
                commit('setCategoryOne', res.data.result)
                resolve(res.data.result)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getCategoryTwo({commit},{parentCategoryCode}){
        return new Promise((resolve, reject) => {
            getCategoryTwo(parentCategoryCode).then(res => {
                commit('setCategoryTwo', res.data.result)
                resolve(res.data.result)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getProDetailBySpuCode({commit},{spuCode}){
        return new Promise((resolve, reject) => {
            getProDetailBySpuCode(spuCode).then(res => {
                commit('setProductDetail', res.data.result)
                resolve(res.data.result)
            }).catch(error => {
                commit('setCategoryOne', {})
                reject(error)
            })
        })
    }

}

export default {
    state,
    mutations,
    actions
}



