import axios from 'axios'
import { getCookie,anonRequestUrls } from './utils';
import store from '@/store'
import router from '@/router'
import { Toast } from 'vant';
import md5 from 'js-md5';




// 创建axios实例
const service = axios.create({
  baseURL: "http://47.101.58.102:8888/shopone/", // api的base_url
  //baseURL:"http://api.shopeone.com/shopone/",
  //baseURL: "http://192.168.3.32:8888/shopone/", // api的base_url
})

const signStr=(config)=>{
  let securityKey='eGdrajp4Z2tqLXNlY3JldA==';
  if(config.params){
    let params=config.params;
    let keyArr = [];
    if(params.sign){
      delete params.sign;
    }
    for (let key in params) {
      if(params[key]!=undefined&&params[key]!==''){
        keyArr.push(key); //属性
      }
    }
    keyArr.sort();
    let signArr=[];
    for (let index = 0; index < keyArr.length; index++) {
      const element = keyArr[index];
        signArr.push('{0}={1}'.format(element,params[element]));
    }
    if(signArr.length>0){
      signArr.push('{0}={1}'.format('key',securityKey));
      // console.log('sign',signArr.join("&"),md5(signArr.join("&")).toUpperCase())
      config.params.sign=md5(signArr.join("&")).toUpperCase();
    }
  }
}

// respone拦截器
service.interceptors.response.use(
  response => {
    let res = response.data
    console.log(res)
    if(res.code==200000){
      return response
    }else{
      Toast.fail(res.msg);
      return Promise.reject(new Error(res.msg))
    }
    // console.log("fetch response:===>"+JSON.stringify(response));
  },
  (error) => {
    console.log(error)
    if(error==503){
      Toast.fail('Please Login');
      router.push({path:'/login',query:{'from':router.currentRoute.fullPath}});
    }else{
      this.bus.$emit('hideLoading');
      Toast.fail('Net work is unavailable, please check it');
    }
    return Promise.reject(error)
  })

service.interceptors.request.use(config => {
  let isAnon=anonRequestUrls.indexOf(config.url)>-1;
  let authorization;
  if(isAnon){
    if(!store.getters.token){
      authorization=store.getters.anonToken;
    }else{
      authorization=store.getters.token.token_type+" "+store.getters.token.access_token ;
    }
  }else{
    if(!store.getters.token){
      console.error(['please login',config.url])
      throw 503;
    }
    authorization=store.getters.token.token_type+" "+store.getters.token.access_token ;
    // authorization=1 ;
  }

  signStr(config);

  config.headers['Authorization'] = authorization
  // config.headers["Auth"] = getCookie(tokenName), //当前cookie 用户 token
  if(!config.headers['Content-Type']){
    config.headers["Content-Type"] = "application/json;charset=UTF-8"
  }
  config.headers["Accept"] = "*/*"

  return config
}, error => {

  console.log(error) // for debug
  return Promise.reject(error)
})


export default service




