import request from '@/common/js/fetch'

export function myCarts() {
  return request({
    url: '/cart/myCarts',
    method: 'get',
  })
}
//勾选商品
export function checkCart(id) {
  const data = {
    id
  }
  return request({
    url: '/cart/checkCart',
    method: 'post',
    params: data
  })
}
//删除商品
export function deleteCart(id) {
  const data = {
    id
  }
  return request({
    url: '/cart/delete',
    method: 'post',
    params: data
  })
}
//批量删除
export function deleteCarts(ids) {
  const data = {
    ids
  }
  return request({
    url: '/cart/deleteBatch',
    method: 'post',
    params: data
  })
}
//更新商品
export function updateCart(id, num, skuCode) {
  const data = {
    id,
    num,
    skuCode
  }
  return request({
    url: '/cart/update',
    method: 'post',
    params: data
  })
}

//添加购物车
export function addCart(id, skuCode, num) {
  const data = {
    id,
    skuCode,
    num
  }
  return request({
    url: '/cart/add',
    method: 'post',
    params: data
  })
}


export function queryBySkuCode(skuCode, num) {
  const data = {
    skuCode,
    num
  }
  return request({
    url: '/cart/desc',
    method: 'get',
    params: data
  })
}
export function synchronizeCart(params) {
  return request({
    url: '/cart/synchronizeCart',
    method: 'post',
    params
  })
}


