<template>
  <!--购物车-->
  <div>
    <van-popup v-model="addChartVisible" position="bottom" v-if="!isShow">
      <div class="addChart">
        <a class="iconfont close" @click="addChartVisible = false">
          &#xe66c;
        </a>
        <div class="pro clearfix">
          <div class="imgWrap">
            <img :src="chartData.spuMainImage" class="img" />
          </div>
          <div class="rightContent">
            <div>
              <span class="price" v-if="selectSku">
                {{
                  (
                    (!!user && !!user.level && user.level * 1 > 1
                      ? selectSku.merchantPrice
                      : selectSku.mallPrice) / 100
                  ).toFixed(2)
                }}
                AED
              </span>
              <span class="price" v-if="!selectSku">
                {{
                  !!user && !!user.level && user.level * 1 > 1
                    ? chartData.vipPriceRange
                    : chartData.priceRange
                }}
                AED
              </span>
              <span
                class="tips_vip"
                v-if="!!user && !!user.level && user.level * 1 > 1"
              >
                VIP
              </span>
              <span class="tips_cutOff">
                {{
                  !!user && !!user.level && user.level * 1 > 1
                    ? chartData.vipPercentage
                    : chartData.percentage
                }}
                OFF
              </span>
            </div>
            <div>
              <span>
                {{
                  selectSku
                    ? (selectSku.price / 100).toFixed(2)
                    : chartData.priceRangeLine
                }}
                AED</span
              >
            </div>
            <div v-if="false"><span class="smallRed"> Only 5 </span></div>
            <div class="sizeValue">{{ selectAttr.join(";") }}</div>
          </div>
        </div>
        <div class="sizeChoose">
          <div
            v-for="(item, index) in productDetail.productAttributeList"
            :key="index"
          >
            <p class="tittle">
              {{ item.name }} <span class="color-res" v-if="false">only 5</span>
            </p>
            <p class="blocks">
              <a
                :class="{ active: i == item.selectIndex, disable: p.disable }"
                v-for="(p, i) in item.productAttributeOptionList"
                :key="i"
                href="javascript:void(0)"
                @click="selectAttribute(index, i, item, p)"
              >
                {{ p.name }}
              </a>
            </p>
          </div>

          <p class="tittle">Number</p>
          <div class="gw_num clearfix">
            <em class="jian" @click="jian">  </em>
            <input type="number" v-model="num" class="num" />
            <em class="add" @click="jia">  </em>
          </div>
          <div class="btnWrap">
            <van-button type="danger" @click="addCartCom" size="large">{{
              id ? "Confirm" : "Add to Cart"
            }}</van-button>
          </div>
        </div>
      </div>
    </van-popup>

    <div class="addChart" v-if="isShow" style="padding-top:0px;">
      <!-- <a class="iconfont close" @click="addChartVisible = false">
        &#xe66c;
      </a> -->
      <div class="pro clearfix">
        <div class="imgWrap">
          <img :src="chartData.spuMainImage" class="img" />
        </div>
        <div class="rightContent">
          <div>
            <span class="price" v-if="selectSku">
              {{
                (
                  (!!user && !!user.level && user.level * 1 > 1
                    ? selectSku.merchantPrice
                    : selectSku.mallPrice) / 100
                ).toFixed(2)
              }}
              AED
            </span>
            <span class="price" v-if="!selectSku">
              {{
                !!user && !!user.level && user.level * 1 > 1
                  ? chartData.vipPriceRange
                  : chartData.priceRange
              }}
              AED
            </span>
            <span
              class="tips_vip"
              v-if="!!user && !!user.level && user.level * 1 > 1"
            >
              VIP
            </span>
            <span class="tips_cutOff">
              {{
                !!user && !!user.level && user.level * 1 > 1
                  ? chartData.vipPercentage
                  : chartData.percentage
              }}
              OFF
            </span>
          </div>
          <div>
            <span>
              {{
                selectSku
                  ? (selectSku.price / 100).toFixed(2)
                  : chartData.priceRangeLine
              }}
              AED</span
            >
          </div>
          <div v-if="false"><span class="smallRed"> Only 5 </span></div>
          <div class="sizeValue">{{ selectAttr.join(";") }}</div>
        </div>
      </div>
      <div class="sizeChoose">
        <div
          v-for="(item, index) in productDetail.productAttributeList"
          :key="index"
        >
          <p class="tittle">
            {{ item.name }} <span class="color-res" v-if="false">only 5</span>
          </p>
          <p class="blocks">
            <a
              :class="{ active: i == item.selectIndex, disable: p.disable }"
              v-for="(p, i) in item.productAttributeOptionList"
              :key="i"
              href="javascript:void(0)"
              @click="selectAttribute(index, i, item, p)"
            >
              {{ p.name }}
            </a>
          </p>
        </div>

        <p class="tittle">Number</p>
        <div class="gw_num clearfix">
          <em class="jian iconfont" @click="jian"> &#xe613; </em>
          <input type="number" readonly='readonly' v-model="num" class="num" />
          <em class="add iconfont" @click="jia"> &#xe715; </em>
        </div>
        <div class="btnWrap">
          <van-button type="danger" @click="addCartCom" size="large">{{
            id ? "Confirm" : "Add to Cart"
          }}</van-button>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import { getSelection } from "@/api/product";
import { addCart, updateCart } from "@/api/cart";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      id: null,
      isShow: false,
      addChartVisible: false,
      chartData: {},
      specData: {},
      num: 1,
      productDetail: {},
      attributeList: [],
      atrributeMap: {},
      selectIndex: [0, "-", 0],
      selectSku: null,
      selectAttr: ["", ""]
    };
  },
  computed: {
    ...mapGetters(["user"])
  },
  created() {
    this.bus.$on("osBack", resCallback => {
      if (this.addChartVisible) {
        this.addChartVisible = false;
      } else {
        // this.$toast(typeof resCallback);
        resCallback("");
        // if(this.$route.path==''){
        // }
      }
    });
  },
  methods: {
    clearData() {
      this.id = null;
      this.num = 1;
      this.addChartVisible = true;
      this.productDetail = {};
      this.attributeList = [];
      this.atrributeMap = {};
      this.isShow = false;
    },
    load(data, isShow,isSetDefault) {
      this.clearData();
      this.chartData = data;
      this.isShow = isShow;
      getSelection(data.spuCode).then(res => {
        this.productDetail = res.data.result;
        this.productDetail.productSkuList.map(t => {
          let attrJson = JSON.parse(t.attributeJson);

          let selectKey = [];
          attrJson.forEach(t => {
            for (const key in t) {
              selectKey.push(t[key]);
            }
          });

          this.attributeList.push(selectKey.join("-"));
          this.atrributeMap[selectKey.join("-")] = t;
        });
        this.selectDefaultSku();
      });
    },
    update(data) {
      this.clearData();
      this.id = data.id;
      this.chartData = data.settlementSku;
      this.num = data.num;
      this.chartData.priceRange = this.chartData.mallPrice;
      this.chartData.priceRangeLine = this.chartData.price;
      this.chartData.spuMainImage = this.chartData.img0;
      getSelection(this.chartData.spuCode).then(res => {
        this.productDetail = res.data.result;
        this.productDetail.productSkuList.map(t => {
          let attrJson = JSON.parse(t.attributeJson);

          let selectKey = [];
          attrJson.forEach(t => {
            for (const key in t) {
              selectKey.push(t[key]);
            }
          });

          this.attributeList.push(selectKey.join("-"));
          this.atrributeMap[selectKey.join("-")] = t;
        });
        this.selectDefaultSku();
      });
    },
    jia() {
      this.num++;
       if(this.selectSku){
        if(this.num>this.selectSku.stock){
          this.num=this.selectSku.stock;
          this.$toast('Your amount is out of stock');
        }
      }
    },
    jian() {
      this.num--;
      if (this.num <= 1) {
        this.num = 1;
      }
    },
    selectAttribute(index, itemIndex, attr, item) {
      if (item.disable) {
        this.$toast('Your amount is out of stock');
        return false;
      }

      this.$set(this.selectIndex, [index == 0 ? 0 : 2], attr.selectIndex==itemIndex?'':item.id);
      this.$set(this.selectAttr, index, attr.selectIndex==itemIndex?'':item.name);
      let sIndex = this.selectIndex.join("");

      this.$set(attr, "selectIndex",  attr.selectIndex==itemIndex?'-1':itemIndex);

      if (this.selectIndex[0] > 0 && this.selectIndex[2] > 0) {
        this.selectSku = this.atrributeMap[sIndex];
      }else{
        this.selectSku=null;
      }

      // 检测数据
      let searchValue =
        (index == 0 ? item.id : "") + "-" + (index == 1 ? item.id : "");
      let arr = this.attributeList.filter(t => {
        return t.indexOf(searchValue) > -1;
      });

      // 默认全黑
      let otherIndex = index == 0 ? 1 : 0;
      this.productDetail.productAttributeList[
        otherIndex
      ].productAttributeOptionList.forEach(s => {
        this.$set(s, "disable", true);
      });
      if (arr.length > 0) {
        arr.forEach(a => {
          let optionId = a.split("-")[otherIndex];
          let selectResult = this.atrributeMap[a];

          if (selectResult.stock > 0) {
            this.productDetail.productAttributeList[
              otherIndex
            ].productAttributeOptionList.forEach(s => {
              if (s.id == optionId) {
                this.$set(s, "disable", false);
              }
            });
          }
        });
      }
    },
    addCartCom(isToCart) {
      if (this.selectSku == null) {
        this.$toast("Please Select Specification");
        const returnEle = document.querySelector("#anchor");
        if (!!returnEle) {
          returnEle.scrollIntoView(true);
        }
      } else {
        if (this.id) {
          this.$store
            .dispatch("updateCart", {
              id: this.id,
              num: this.num,
              skuCode: this.selectSku.skuCode
            })
            .then(res => {
              this.bus.$emit("loadIndexData");
              if (!this.$store.getters.token) {
                this.bus.$emit("reloadCart");
              } else {
                this.$store.dispatch("myCarts");
              }
              this.$toast("Edit Successfully");
              this.addChartVisible = false;
            });
        } else {
          this.$store
            .dispatch("addCart", {
              skuCode: this.selectSku.skuCode,
              num: this.num
            })
            .then(res => {
              this.bus.$emit("loadIndexData");
              this.$toast("Add Successfully");
              // if (isToCart) {
              //   this.$router.push("/cart");
              // } else {
              this.addChartVisible = false;
              if (!this.$store.getters.token) {
                this.bus.$emit("reloadCart");
              } else {
                this.$store.dispatch("myCarts");
              }

              if (this.isShow) {
                this.$store.dispatch("closeView");
              }
              // }
            });
        }
      }
    },
    addCart() {
      this.addCartCom();
    },
    addCartGo() {
      this.addCartCom(true);
    },
    selectDefaultSku() {
      let index = -1,
        select;
      for (let i = 0; i < this.productDetail.productSkuList.length; i++) {
        const element = this.productDetail.productSkuList[i];
        if (element.stock > 0) {
          select = element;
          index = i;
          break;
        }
      }

      if (index > -1) {
        this.selectSku = select;
        let selectKey = [];
        JSON.parse(this.selectSku.attributeJson).forEach(t => {
          for (const key in t) {
            selectKey.push(t[key]);
          }
        });

        let firstIndex = this.productDetail.productAttributeList[0].productAttributeOptionList
          .map(t => {
            return t.id;
          })
          .indexOf(selectKey[0] * 1);
        let secondIndex = this.productDetail.productAttributeList[1].productAttributeOptionList
          .map(t => {
            return t.id;
          })
          .indexOf(selectKey[1] * 1);
        this.selectAttribute(
          0,
          firstIndex,
          this.productDetail.productAttributeList[0],
          this.productDetail.productAttributeList[0].productAttributeOptionList[
            firstIndex
          ]
        );
        this.selectAttribute(
          1,
          secondIndex,
          this.productDetail.productAttributeList[1],
          this.productDetail.productAttributeList[1].productAttributeOptionList[
            secondIndex
          ]
        );
      } else {
        if (this.productDetail.productSkuList.length > 0) {
          let selectKey = [];
          JSON.parse(
            this.productDetail.productSkuList[0].attributeJson
          ).forEach(t => {
            for (const key in t) {
              selectKey.push(t[key]);
            }
          });

          let firstIndex = this.productDetail.productAttributeList[0].productAttributeOptionList
            .map(t => {
              return t.id;
            })
            .indexOf(selectKey[0] * 1);

          this.selectAttribute(
            0,
            firstIndex,
            this.productDetail.productAttributeList[0],
            this.productDetail.productAttributeList[0]
              .productAttributeOptionList[firstIndex]
          );
        }
      }
    }
  }
};
</script>

<style>
</style>



