import request from '@/common/js/fetch'

export function getProListByName(params) {

  return request({
    url: '/productSpu/getProListByName',
    method: 'get',
    params
  })
}

export function getCategoryOne(level) {
  const data = {
    level:level
  }
  return request({
    url: '/productCategory/getCategoryOne',
    method: 'get',
    params:data
  })
}

export function getCategoryTwo(parentCategoryCode) {
  const data = {
    parentCategoryCode:parentCategoryCode
  }
  return request({
    url: '/productCategory/getCategoryTwo',
    method: 'get',
    params:data
  })
}

export function getProDetailBySpuCode(spuCode) {
  const data = {
    spuCode:spuCode
  }
  return request({
    url: '/productSpu/getProDetailBySpuCode',
    method: 'get',
    params:data
  })
}
//规格接口
export function productSpuSelection(id1,id2,id3,id4){
    const data=`[{${id1},${id2}},{${id3},${id4}}]`
      
  return request({
    url: '/productSpu/selection',
    method: 'get',
    params:data
  })
}

//评论列表
export function getCommentList(params){
 
return request({
  url: '/productSpu/commentList',
  method: 'get',
  params
})
}
//获取商品属性
export function getSelection(spuCode) {
  const data = {
    spuCode
  }
  return request({
    url: '/productSpu/selection',
    method: 'get',
    params:data
  })
}
