import request from '@/common/js/fetch'

export function getFlashSaleDay(pageNo,pageSize) {
  return request({
    url: '/activityPromotion/getFlashSaleDay',
    method: 'get',
    params:{pageNo,pageSize}
  })
}

export function getMarketingActivity() {
  return request({
    url: '/activityPromotion/getMarketingActivity',
    method: 'get'
  })
}

export function getAllPromotionalMaterialType() {
  return request({
    url: '/activityPromotion/getAllPromotionalMaterialType',
    method: 'get'
  })
}

export function getAllPromotionalMaterialByType(params) {
  return request({
    url: '/activityPromotion/getAllPromotionalMaterialByType',
    method: 'get',
    params
  })
}

export function shareActivityMaterial(params) {
  return request({
    url: '/activityPromotion/shareActivityMaterial',
    method: 'get',
    params
  })
}

export function shareActivityMaterialMultiImg(params) {
  return request({
    url: '/activityPromotion/shareActivityMaterialMultiImg',
    method: 'get',
    params
  })
}



