import request from '@/common/js/fetch'

export function myWishes() {
  return request({
    url: '/wish/myWishes',
    method: 'get'
  })
}

export function addWish(params) {
    return request({
      url: '/wish/add',
      method: 'post',
      params
    })
  }

  export function deleteWish(params) {
    return request({
      url: '/wish/delete',
      method: 'post',
      params
    })
  }


