const getters = {
  user: state => {
    return state.user.info
  },
  loginName: state => {
    return state.user.loginName
  },
  loginStatus: state => {
    return state.user.loginStatus
  },
  token: state => {
    return state.user.token
  },
  anonToken:state=>{
    return state.user.anonToken
  },
  language:state=>{
    return state.user.language;
  },
  indexData:state=>{
    return state.index.indexData;
  },
  productData:state=>{
    return state.index.productData;
  },
  searchs:state=>{
    return state.user.searchs
  },
  searchProductList:state=>{
    return state.product.searchProductList
  },
  categoryOne:state=>{
    return state.product.categoryOne
  },
  categoryTwo:state=>{
    return state.product.categoryTwo
  },
  productDetail:state=>{
    return state.product.productDetail
  },
  flashShareDay:state=>{
    return state.flashSale.flashSaleDay
  },
  wishes:state=>{
    return state.wish.wishes
  },
  wishCode:state=>{
    return state.wish.wishCode
  },
  countryCode:state=>{
    return state.user.countryCode
  },
  carts:state=>{
    return state.cart.carts
  },
  selectAddress:state=>{
    return state.user.selectAddress
  },
  browsers:state=>{
    return state.user.browsers
  },
  proDetail:state=>{
    return state.cart.proDetail
  }
}

export default getters



