<template>
  <div id="app">
    <v-touch
      v-on:swipeleft="onSwipeLeft"
      v-on:swiperight="onSwipeRight"
      :swipe-options="{ direction: 'horizontal', threshold: 80 }"
      tag="div"
    >
      <router-view />
    </v-touch>
    <drawer ref="appDrawer" />
    <div class="pageLoading" v-show="loading"></div>
  </div>
</template>

<script>
import { setTimeout } from "timers";
export default {
  name: "App",
  data() {
    return {
      isAndroid: false,
      isIOS: false,
      loading: false
    };
  },
  created() {
    if (this.$store.getters.token) {
      this.$store.dispatch("myWishes").then(res => {});
      this.$store.dispatch("userInfo").then(res => {});
    }

    this.bus.$on("showDrawer", this.showDrawer);
    this.bus.$on("loadIndexData", this.loadIndexData);
    this.bus.$on("showLoading", () => {
      this.loading = true;
    });
    this.bus.$on("hideLoading", () => {
      this.loading = false;
    });

    this.$store
      .dispatch("isAndroid")
      .then(res => {
        this.isAndroid = true;
      })
      .catch(e => {
        this.isAndroid = false;
      });

    this.$store
      .dispatch("isIOS")
      .then(res => {
        this.isIOS = true;
      })
      .catch(e => {
        this.isIOS = false;
      });

    this.$store.dispatch("listenBack", {
      callback: resCallback => {
        this.bus.$emit("osBack", resCallback);
      }
    });

    this.$store.dispatch("syncListener", {
      callback: ({ key, value }, resCallback) => {
        this.$toast(value);
        let isClear = value == "";
        switch (key) {
          case "user.info":
            if (isClear) {
              this.$store.dispatch("commitClearUser");
            }
            break;
          case "token":
            if (isClear) {
              this.$store.dispatch("commitClearUser");
            }else{
              this.$store.dispatch("commitToken",params);

            }
            break;
          case "addCart":
            let params = JSON.parse(value);
            this.$store.dispatch("addCart", params);
            break;
          case "proDetail":
            this.$store.dispatch("proDetail", { proDetail: JSON.parse(value) });
            break;
        }
      }
    });
  },
  methods: {
    onSwipeLeft() {
      console.log("swipe left");
    },
    onSwipeRight(e) {
      console.log('swipe right');
      if (e.center.x - e.deltaX > window.screen.width*0.24) {
        return false;
      }
      if (this.isIOS) {
        if (this.$route.path != "/") {
          this.$router.go(-1);
        } else {
          this.showDrawer();
        }
      } else if (this.isAndroid) {
        this.showDrawer();
      }
    },
    showDrawer() {
      console.log(1);
      this.$refs.appDrawer.showDrawer();
    },
    showLoading() {},
    loadIndexData(isNeedLocation) {
      if (!isNeedLocation) {
        let position=localStorage.getItem('position');
        if(position){
          position=JSON.parse(position);
          this.$store.dispatch("indexData",{ lon: position.longitude, lan: position.latitude }).then(r => {});
        }else{
          this.$store.dispatch("indexData").then(r => {});
        }
      } else {
        this.$store.dispatch("getUserInfo").then(res => {
          localStorage.setItem('position',JSON.stringify(res));
          this.$store
            .dispatch("indexData", { lon: res.longitude, lan: res.latitude })
            .then(r => {});
        });
      }
    }
  },
  mounted() {
    this.loadIndexData();
  },
  watch: {
    "$store.getters.token": function(val) {
      if (val) {
        this.$store.dispatch("myWishes").then(res => {});

        this.$store.dispatch("userInfo").then(r => {
          if (!r.superiorId) {
            this.$router.push("fillInvitation");
          }
        });

        this.$store.dispatch("syncCarts");
        this.$store.dispatch("syncBrowserRecord");
      }
    },
    "$route.matched": function(val) {
      //  console.log(val);
    }
  }
};
</script>

<style lang="scss">
@import "./common/css/common.scss";
</style>



