import Cookies from 'js-cookie'
import { getUserInfo, login, logout, register, addUserBrowseProduct, getUserBrowseProduct, deleteUserBrowseProduct } from '@/api/user'
import { getProListBySpuList } from '@/api/product'
import * as app from '@/common/js/app'
import store from '../index'

let getLocalStorge = (key) => {
  if (localStorage.getItem(key)) {
    try {
      return JSON.parse(localStorage.getItem(key));
    } catch (e) {
      console.log(e);
      localStorage.removeItem(key);
    }
  }
}
if(!getLocalStorge('token')){
  localStorage.removeItem('user.info');
}

const state = {
  loginStatus: !getLocalStorge("token") ? false : true,
  info: getLocalStorge('user.info') || null,
  token: getLocalStorge('token'),
  anonToken: "Basic eGdrajp4Z2tqLXNlY3JldA==",
  loginName: Cookies.get("loginName"),
  language: Cookies.get('language'),
  searchs: getLocalStorge('recent.research') || [],
  countryCode: getLocalStorge('countryCode') || null,
  selectAddress: getLocalStorge('selectAddress') || null,
  browsers: getLocalStorge('browsers') || []
}


const mutations = {
  setUser(state, user) {
    state.loginStatus = true;
    state.info = user;
    localStorage.setItem('user.info', JSON.stringify(user));
    app.sync('user.info', JSON.stringify(user))
  },
  updateLoadingStatus(state, payload) {
    state.isLoading = payload.isLoading
  },
  clearUser(state) {
    console.warn("login expire, clear login status");
    state.loginStatus = false;
    state.info = null;
    state.token = null;
    localStorage.removeItem("token");
    localStorage.removeItem('user.info');
    app.sync('user.info', '')
    app.sync('token', '')
  },
  setLoginName(state, loginName) {
    state.loginName = loginName;
  },
  setCountryCode(state, conuntryCode) {
    state.conuntryCode = conuntryCode;
    localStorage.setItem("countryCode", conuntryCode);
  },
  setLanguage(state, language) {
    state.language = language;
  },
  setToken(state, token) {
    state.token = token;
    localStorage.setItem("token",  JSON.stringify(token));
    app.sync('token', JSON.stringify(token))
  },
  setSearch(state, search) {
    if (state.searchs.indexOf(search) > -1) {
      state.searchs.splice(state.searchs.indexOf(search), 1);
    }
    state.searchs.unshift(search);
    localStorage.setItem('recent.research', JSON.stringify(state.searchs));
  },
  setSelectAddress(state, selectAddress) {
    state.selectAddress = selectAddress;
    localStorage.setItem('selectAddress', JSON.stringify(state.selectAddress));
  },
  clearSelectAddress(state) {
    state.selectAddress = null;
    localStorage.removeItem('selectAddress');
  },
  clearSearch(state) {
    state.searchs = [];
    localStorage.removeItem('recent.research');
  },
  addBrowser(state, spuCode) {
    state.browsers.unshift(spuCode);
    localStorage.setItem('browsers', JSON.stringify(state.browsers));
  },
  deleteBrowser(state, index) {
      state.browsers.splice(index, 1);
      localStorage.setItem('browsers', JSON.stringify(state.browsers));
  },
  clearBrower(state) {
    state.browsers = [];
    localStorage.removeItem('browsers');
  }
}

const actions = {
  commitUser: ({ commit }, user) => commit('setUser', user),
  changeLocation: ({ commit }, conuntryCode) => commit('setCountryCode', conuntryCode),
  commitClearUser: ({ commit }) => commit('clearUser'),
  commitLoginName: ({ commit }, loginName) => commit('setLoginName', loginName),
  commitLanguage: ({ commit }, language) => commit('setLanguage', language),
  commitSearch: ({ commit }, { search }) => commit('setSearch', search),
  commitSelectAddress: ({ commit }, { selectAddress }) => commit('setSelectAddress', selectAddress),
  commitToken: ({ commit }, token) => commit('setToken', token),
  clearSearch: ({ commit }) => commit('clearSearch'),
  clearSelectAddress: ({ commit }) => commit('clearSelectAddress'),
  login({ commit }, { username, password }) {
    return new Promise((resolve, reject) => {
      login(username, password).then(res => {
        commit('setToken', res.data.result)
        resolve()
      }).catch(error => {
        reject(error)
      })
    })
  },
  register({ commit }, params) {
    return new Promise((resolve, reject) => {
      register(params).then(res => {
        commit('setToken', res.data.result.token)
        resolve()
      }).catch(error => {
        reject(error)
      })
    })
  },
  logout({ commit }) {
    return new Promise((resolve, reject) => {
      logout().then(res => {
        commit('clearUser', res.data)
        commit('clearCart');
        commit('clearBrower');
        resolve()
      }).catch(error => {
        reject(error)
      })
    })
  },
  userInfo({ commit }) {
    return new Promise((resolve, reject) => {
      getUserInfo().then(res => {
        commit('setUser', res.data.result)
        resolve(res.data.result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  addBrowserRecord({ commit }, { product }) {
    if (!store.getters.token) {
      commit('addBrowser', product.spuCode);
    }
  },
  syncBrowserRecord({ commit }) {
    if (store.getters.browsers.length > 0) {
      addUserBrowseProduct({
        spuCodeList: store.getters.browsers.map(t => { return t.spuCode }).join(',')
      }).then(res => {
        commit('clearBrower');
      });
    }
  },
  getUserBrowseProduct({ commit }, params) {

    return new Promise((resolve, reject) => {
      if (store.getters.token) {
        return getUserBrowseProduct(params).then(res => {
          resolve(res.data.result);
        }).catch(e => {
          reject();
        });
      } else {
        if(store.getters.browsers.length>0){
          getProListBySpuList({ spuCodeList: store.getters.browsers.join(',') }).then(res => {
            resolve(res.data.result);
          }).catch(e => {
            reject();
          });
        }else{
          resolve([]);
        }
      }
    });
  },
  deleteUserBrowseProduct({ commit }, params) {
    return new Promise((resolve, reject) => {
      if (store.getters.token) {
        return deleteUserBrowseProduct(params).then(res => {
          resolve(res);
        }).catch(e => {
          reject();
        });
      } else {
        commit('deleteBrowser', params.index);
        resolve();
      }
    });
  }

}

export default {
  state,
  mutations,
  actions
}



