import { myWishes,addWish,deleteWish } from '@/api/wish'

const state = {
    wishes: {},
    wishCode:[]
}


const mutations = {
    setWishes(state, wishes) {
        state.wishes = wishes;
        state.wishCode=wishes.result.map(t=>{return t.spuCode;});
    }
}


const actions = {
    myWishes({ commit }) {
        return new Promise((resolve, reject) => {
            myWishes().then(res => {
                commit('setWishes', res.data.result)
                resolve(res)
            }).catch(error => {
                commit('setWishes',{result:[]})
                reject(error)
            })
        })
    },
    addWish({ commit }, param) {
        return new Promise((resolve, reject) => {
            addWish(param).then(res => {
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    deleteWish({ commit }, param) {
        return new Promise((resolve, reject) => {
            deleteWish(param).then(res => {
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    }
}

export default {
    state,
    mutations,
    actions
}



