import * as app from '@/common/js/app'
import {shareBaseUrl} from '@/common/js/utils'
import Cookies from 'js-cookie'


const state = {
    appVersion: {},
   
}


const mutations = {
    setAppVersion(state, appVersion) {
        state.appVersion = appVersion;
    }
}


const actions = {
    appVersion({ commit }) {
        return new Promise((resolve, reject) => {
            app.appUpdate("http://static.ikedou.net/xiaoguo/dist.zip","1","0").then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    showHeader({ commit }) {
        return new Promise((resolve, reject) => {
            app.showHeader().then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    hideHeader({ commit }) {
        return new Promise((resolve, reject) => {
            app.hideHeader().then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    getUserInfo({ commit }) {
        return new Promise((resolve, reject) => {
            app.getUserInfo().then(res => {
            
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    thirdLogin({ commit },{type}) {
        return new Promise((resolve, reject) => {
            app.thirdLogin(type).then(res => {
            
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    share({ commit },params) {
        return new Promise((resolve, reject) => {
            params.url=shareBaseUrl+params.url;
            if(!params.content){
                params.content="Check this out. It has a very huge discount. I am sure you are going to love it!";
            }
            if(!params.needPic){
                delete params.imageUrl;
            }
            app.share(params).then(res => {
                
                resolve(res)
            }).catch(error => {
                reject(error)
            })
        })
    },
    isApp({ commit },params){
        return app.isApp();
    },
    isAndroid({ commit },params){
        return app.isAndroid();
    },
    isIOS({ commit },params){
        return app.isIOS();
    },
    closeApp({ commit },params){
         Cookies.set('isShowGetApp','1');
    },
    openApp({ commit },params){
        return app.openApp(params);
    },
    isShowApp({ commit },params){
      return new Promise((resolve,reject)=>{
          if(Cookies.get('isShowGetApp')){
                resolve();
          }else{
            reject();
          }
      }) ;
   },
   listenBack({commit},{callback}){
    app.listenBack(callback);
   },
   syncListener({commit},{callback}){
    app.syncListener(callback);
   },
   webBack({commit}){
    app.webBack();
   },
   closeView({commit}){
    app.closeView();
   },
   syncFromOS({commit},{key,value}){

   },
    location({commit},params){
        return new Promise((resolve, reject) => {
            if (navigator.geolocation){
                navigator.geolocation.getCurrentPosition((position)=>{
                    console.log(position);
                    resolve(position)
            }
            ,(error)=>{
                switch(error.code) {
                  case error.PERMISSION_DENIED:
                    reject(new Error("定位失败,用户拒绝请求地理定位"));
                    break;
                  case error.POSITION_UNAVAILABLE:
                    reject(new Error("定位失败,位置信息是不可用"));
                    break;
                  case error.TIMEOUT:
                    reject(new Error("定位失败,请求获取用户位置超时"));
                    break;
                  case error.UNKNOWN_ERROR:
                    reject(new Error("定位失败,定位系统失效"));
                    break;
                }
              });
            }else{
                reject(new Error("浏览器不支持地理定位"));
              }
        })
    }
}

export default {
    state,
    mutations,
    actions
}



